import Vue from 'vue';
import vuetify from './plugins/vuetify';
import VueRouter from 'vue-router';

import App from './App.vue';

// Router Components
import Home from '@/components/Home';
import NotFound from '@/components/NotFound';

// Customized Components
import CardIcon from '@/components/material/CardIcon';
import CardItem from '@/components/material/CardItem';
import Divider from '@/components/material/Divider';
import ProgressCircular from '@/components/material/ProgressCircular';
import TextHint from '@/components/material/TextHint';
import TextLong from '@/components/material/TextLong';
import TextTitle from '@/components/material/TextTitle';

Vue.config.productionTip = false;
Vue.config.devtools = false;

Vue.use(VueRouter);

Vue.component('CardIcon', CardIcon);
Vue.component('CardItem', CardItem);
Vue.component('Divider', Divider);
Vue.component('ProgressCircular', ProgressCircular);
Vue.component('TextHint', TextHint);
Vue.component('TextLong', TextLong);
Vue.component('TextTitle', TextTitle);

const router = new VueRouter({
  base: '/',
  mode: 'hash',
  scrollBehavior () {
    return { x: 0, y: 0 };
  },
  routes: [
    { path: '*', name: 'NotFound', component: NotFound },
    { path: '/', name: 'Home', component: Home },
    { path: '/home', redirect: '/' }
  ]
});

new Vue({
  vuetify,
  router,
  render: h => h(App)
}).$mount('#app');
