import axios from 'axios';

const API_HOST = 'https://api.airtable.com/v0';
const API_KEY = 'keyy3UX3EEfpn0bkt';
const DATABASE = 'appqcXs75u1ztbEZk';

const getTable = function (table) {
  return new Promise((resolve) => {
    axios
      .get(`${API_HOST}/${DATABASE}/${table}?api_key=${API_KEY}&view=Grid%20view`)
      .then((response) => {
        resolve(response.data.records);
      });
  });
};

export default {
  getTable
};
