<template>
  <v-card class="mx-auto" :width="isMobile() ? '100%' : '80%'" :href="link" target="_blank" :color="color.panel.grey" dark>
    <v-card-title class="justify-center align-center"><v-icon size="60">{{ icon }}</v-icon><div class="ml-4">{{ name }}</div></v-card-title>
  </v-card>
</template>

<script>
import color from '@/lib/color';

export default {
  name: 'CardIcon',

  props: {
    name: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      required: true
    },
    link: {
      type: String,
      required: true
    }
  },

  data () {
    return {
      color: color
    };
  },

  methods: {
    isMobile () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        case 'md':
          return true;
        case 'lg':
          return false;
        case 'xl':
          return false;
      }
    }
  }
};
</script>
