<template>
  <v-container>
    <v-img class="mx-auto my-16" max-width="500" :src="require('@/assets/logoRectangle.jpg')" />

    <ProgressCircular v-if="!intro" />
    <TextLong v-else class="ma-16" :text="intro" align="center" />

    <ProgressCircular v-if="!foods" />
    <div v-else>
      <Divider />
      <TextTitle title="主食．Meals" />
      <v-row>
        <v-col cols="12" v-for="food in foods" :key="food.id"><CardItem :name="food.name" :icon="food.icon" :items="food.items" /></v-col>
      </v-row>
    </div>

    <ProgressCircular v-if="!drinks" />
    <div v-else>
      <Divider class="mt-16" />
      <TextTitle title="飲料．Drinks" />
      <TextHint class="ma-8" :text="'（ 中杯容量為 500ml / 大杯容量為 700ml ）'" align="center" />
      <v-row>
        <v-col cols="12" v-for="drink in drinks" :key="drink.id"><CardItem :name="drink.name" :icon="drink.icon" :items="drink.items" /></v-col>
      </v-row>
    </div>

    <ProgressCircular v-if="!frieds" />
    <div v-else>
      <Divider class="mt-16" />
      <TextTitle title="炸物．Snacks" />
      <v-row>
        <v-col cols="12" v-for="fried in frieds" :key="fried.id"><CardItem :name="fried.name" :icon="fried.icon" :items="fried.items" /></v-col>
      </v-row>
    </div>

    <ProgressCircular v-if="!map" />
    <div v-else>
      <Divider class="mt-16" />
      <TextTitle title="地點．Location" />
      <div class="text-center">
        <iframe :src="map" width="90%" height="500px" style="border: 0;" loading="lazy"></iframe>
      </div>
    </div>

    <ProgressCircular v-if="!(link.facebook && link.instagram)" />
    <div v-else>
      <Divider class="mt-16" />
      <TextTitle title="聯絡．Contact" />
      <v-row>
        <v-col><CardIcon name="Facebook" icon="mdi-facebook" :link="link.facebook" /></v-col>
        <v-col><CardIcon name="Instagram" icon="mdi-instagram" :link="link.instagram" /></v-col>
      </v-row>
    </div>

  </v-container>
</template>

<script>
import apiAirtable from '@/lib/apiAirtable';
import color from '@/lib/color';

export default {
  name: 'Home',

  data () {
    return {
      color: color,
      intro: null,
      foods: null,
      drinks: null,
      frieds: null,
      map: null,
      link: {
        facebook: null,
        instagram: null
      }
    };
  },

  created () {
    this.$vuetify.theme.dark = false;
  },

  beforeMount () {
    this.initIntro();
    this.initFood();
    this.initDrink();
    this.initFried();
    this.initMap();
    this.initFacebook();
    this.initInstagram();
  },

  methods: {
    initIntro: async function () {
      const datas = await apiAirtable.getTable('MISC');

      for (const data of datas) {
        if (data.fields.name === 'intro') {
          this.intro = data.fields.content;
          break;
        }
      }
    },
    initFood: async function () {
      const foods = [];
      const datas = await apiAirtable.getTable('Food');

      for (const data of datas) {
        const name = data.fields.name;
        const icon = data.fields.icon;
        const items = [];
        for (const item of Object.keys(data.fields)) {
          if (!['name', 'icon', 'last_modified_at'].includes(item) && data.fields[item] !== 0) {
            items.push({ name: item, price: data.fields[item] });
          }
        }

        items.sort(function (a, b) {
          const keyA = a.price;
          const keyB = b.price;
          if (keyA < keyB) return -1;
          if (keyA > keyB) return 1;
          return 0;
        });

        foods.push({ name: name, icon: icon, items: items });
      }

      this.foods = foods;
    },
    initDrink: async function () {
      const drinks = [];
      const datas = await apiAirtable.getTable('Drink');

      for (const data of datas) {
        const name = data.fields.name;
        const icon = data.fields.icon;
        const items = [];
        for (const item of Object.keys(data.fields)) {
          if (!['name', 'icon', 'last_modified_at'].includes(item) && data.fields[item] !== 0) {
            items.push({ name: item, price: data.fields[item] });
          }
        }

        items.sort(function (a, b) {
          const keyA = a.price;
          const keyB = b.price;
          if (keyA < keyB) return -1;
          if (keyA > keyB) return 1;
          return 0;
        });

        drinks.push({ name: name, icon: icon, items: items });
      }

      this.drinks = drinks;
    },
    initFried: async function () {
      const frieds = [];
      const datas = await apiAirtable.getTable('Fried');

      for (const data of datas) {
        const name = data.fields.name;
        const icon = data.fields.icon;
        const items = [];
        for (const item of Object.keys(data.fields)) {
          if (!['name', 'icon', 'last_modified_at'].includes(item) && data.fields[item] !== 0) {
            items.push({ name: item, price: data.fields[item] });
          }
        }

        items.sort(function (a, b) {
          const keyA = a.price;
          const keyB = b.price;
          if (keyA < keyB) return -1;
          if (keyA > keyB) return 1;
          return 0;
        });

        frieds.push({ name: name, icon: icon, items: items });
      }

      this.frieds = frieds;
    },
    initMap: async function () {
      const datas = await apiAirtable.getTable('MISC');

      for (const data of datas) {
        if (data.fields.name === 'map') {
          this.map = data.fields.content;
          break;
        }
      }
    },
    initFacebook: async function () {
      const datas = await apiAirtable.getTable('MISC');

      for (const data of datas) {
        if (data.fields.name === 'facebook') {
          this.link.facebook = data.fields.content;
          break;
        }
      }
    },
    initInstagram: async function () {
      const datas = await apiAirtable.getTable('MISC');

      for (const data of datas) {
        if (data.fields.name === 'instagram') {
          this.link.instagram = data.fields.content;
          break;
        }
      }
    }
  }
};
</script>

<style scoped>
iframe {
  max-width: 1000px;
}
</style>
