<template>
  <v-card class="mx-auto" width="90%" max-width="1000px" outlined>
    <v-card-title>
      <v-icon>{{ icon }}</v-icon>
      <span class="ml-4 font-weight-regular">{{ name }}</span>
      <v-spacer />
      <span v-if="!isMobile()">
        <v-btn :color="color.panel.grey" v-for="item in items" :key="item.id" class="ma-1 disable-events text-caption font-weight-bold" width="140" outlined rounded>{{ item.name }}．{{ item.price }} 元</v-btn>
      </span>
    </v-card-title>

    <div v-if="isMobile()">
      <div v-for="item in items" :key="item.id" class="px-4 py-1">
        <v-btn :color="color.panel.grey" class="mb-2 disable-events text-caption font-weight-bold" block outlined rounded>{{ item.name }}．{{ item.price }} 元</v-btn>
      </div>
    </div>
  </v-card>
</template>

<script>
import color from '@/lib/color';

export default {
  name: 'CardItem',

  props: {
    name: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      required: true
    },
    items: {
      type: Array,
      required: true
    }
  },

  data () {
    return {
      color: color
    };
  },

  methods: {
    isMobile () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        case 'md':
          return true;
        case 'lg':
          return false;
        case 'xl':
          return false;
      }
    }
  }
};
</script>
